.featured__title {
  margin-bottom: 4px;
}

.featured__subtitle {
  display: block;
  margin-bottom: 18px;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  text-transform: uppercase;
}

.featured__description,
.featured__list {
  margin: 0;
  margin-bottom: 28px;
}

.featured__item {
  display: flex;
  box-sizing: border-box;
  border-bottom: 1px solid #ececec;
}

.featured__attribute,
.featured__value {
  padding: 10px 0;
  line-height: 20px;
}

.featured__attribute {
  min-width: 100px;
}

.featured__value {
  margin: 0;
  flex-grow: 1;
}

.featured__image {
  display: block;
  margin-bottom: 26px;
}

.featured__price {
  margin: 0;
  margin-bottom: 24px;
  font-size: 17px;
  font-weight: 700;
  text-align: center;
  color: #444444;
}

.featured__button {
  margin-bottom: 36px;
}


@media (min-width: $tablet-width) {
  .featured {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 0 42px;
  }

  .featured__wrapper {
    display: flex;
    justify-content: space-between;
    grid-column: 1/-1;
  }

  .featured__subtitle {
    width: 100px;
    text-align: center;
  }

  .featured__image {
    grid-column: 2/3;
    grid-row: 2/6;
  }
}

@media (min-width: $desktop-width) {
  .featured {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 0 58px;
    padding-left: 225px;
  }

  .featured__wrapper {
    display: flex;
    justify-content: flex-start;
    grid-column: 1/-1;
    grid-row: 1/2;
    z-index: 2;
    margin-left: -142px;
  }

  .featured__title {
    order: 2;
  }

  .featured__subtitle {
    margin-right: 42px;
  }

  .featured__image {
    grid-column: 2/3;
    grid-row: 1/6;
  }
}
